import React, { lazy } from "react";
import { Component } from "react";
import { motion } from "framer-motion";
import Navbar from "./navigation-bar/navbar";
import { slide as Menu } from "react-burger-menu";
import MenuLinks from "../MenuLinks";
import closeIcon from "../../assets/images/close-icon.svg";
import burgerIcon from "../../assets/images/menu-icon.svg";
import "./header.scss";

const JoinusPage = lazy(() => import("./join-us-page/joinus-page"));
const animation = {
  hidden: {
    opacity: 0,
  },
  visible: {
    opacity: 1,
    transition: {
      duration: 1,
    },
  },
};

class Header extends Component {
  state = {
    isOpen: false,
    timesRendered: 0,
  };

  toggle = () => {
    this.setState({
      isOpen: !this.state.isOpen,
      timesRendered: this.state.timesRendered + 1,
    });
  };

  reactToToggles = () => {
    if (this.state.isOpen) {
      return (
        <div id={"compact-menu"}>
          <a href={"https://www.roshan-ai.ir/"} id={"mobile-menu-link"}>
             
          </a>
          <a href={"https://www.roshan-ai.ir/about-us"} id={"mobile-menu-link"}>
             
          </a>
          <a href={"https://www.roshan-ai.ir/about-us"} id={"mobile-menu-link"}>
              
          </a>
          <a href={"https://www.roshan-ai.ir/about-us"} id={"mobile-menu-link"}>
              
          </a>
        </div>
      );
    } else {
      return <JoinusPage shouldRerender={this.state.timesRendered === 0} />;
    }
  };

  render() {
    return (
      <motion.div
        variants={animation}
        initial='hidden'
        animate='visible'
        id={"all"}
        style={{ background: this.state.isOpen && "black" }}
      >
        <Menu
          menuClassName={"menu"}
          itemListElement='div'
          burgerButtonClassName={"my-button"}
          pageWrapId={"header-wrapper"}
          itemListClassName={"menu-items"}
          crossButtonClassName={"my-cross"}
          outerContainerId={"all"}
          customBurgerIcon={<img src={burgerIcon} alt={""} />}
          customCrossIcon={
            <div>
              <img src={closeIcon} alt={""} />
            </div>
          }
        >
          <MenuLinks />
        </Menu>
        <div className='header-wrapper' id={"header-wrapper"}>
          <Navbar toggle={this.toggle} isOpen={this.state.isOpen} />
          {this.reactToToggles()}
        </div>
      </motion.div>
    );
  }
}

export default Header;
